/* Specifik styling  */

#assets .column {
  padding: 3vw;
  background-color: vars.$white;
  box-shadow: -12px -12px 20px rgba(245, 245, 245, 0.5), 12px 12px 20px rgba(162, 180, 198, 0.3);
}

#assets .row {
  padding: 5vw;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 5vw;
  margin: 0 auto;
  max-width: 2000px;
}

#assets h2 {
  margin: 3vw 3vw 3vw 5vw;
}

#assets p {
  min-height: 70px;
}
#assets img {
  width: 50%;
}

#assets figure {
  text-align: center;
  margin: 1vw;
}

#video.container {
  background-color: vars.$black;
  height: 60vh;
  margin: 0;
}

#blog.container {
  margin: 5vw 15vw;
}

#blog .row {
  padding: 5vw;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(40%, 1fr));
  grid-gap: 5vw;
  margin: 0 auto;
  max-width: 1200px;
}

#blog .column {
  background-color: vars.$black;
  padding: 0 0 3vw 0;
}

#blog h3,
#blog p {
  padding: 0 2vw;
  color: vars.$white;
}

#blog img {
  height: 20vh;
  object-fit: cover;
}

#blog figure {
  width: 100%;
  padding: 0;
  margin: 0;
}

#blog button {
  margin: 0 0 0 2vw;
}

#blog .blog_title {
  max-height: 200px;
}
#contact.container {
  background-color: vars.$black;
  margin: 0;
  height: 50vh;
}
